// 考试报名表单
<template>
  <div id="TestRegistrationForm">
    <div class="box">
      <van-form @submit="onSubmit" :show-error-message="false">
        <section>
          <div class="section_title"><span></span> <span>科目信息</span></div>
          <van-field
            v-model="username"
            name="报考点"
            label="报考点"
            placeholder="请选择下属单位"
            :is-link="true"
            @click="fieldClick('报考点')"
            :input-align="right"
          />
          <van-field
            v-model="username"
            name="考试类型"
            label="考试类型"
            placeholder="请选择"
            :is-link="true"
            @click="fieldClick('考试类型')"
          />
          <van-field
            v-model="username"
            name="考点"
            label="考点"
            placeholder="请选择"
            :is-link="true"
            @click="fieldClick('考点')"
          />
          <van-field
            v-model="username"
            name="报考科目"
            label="报考科目"
            placeholder="请选择"
            :is-link="true"
            @click="fieldClick('报考科目')"
          />
          <van-field
            v-model="username"
            name="报考级别"
            label="报考级别"
            placeholder="请选择"
            :is-link="true"
            @click="fieldClick('报考级别')"
          />
          <van-field
            v-model="username"
            name="辅导课"
            label="辅导课"
            placeholder="请选择"
            :is-link="true"
            @click="fieldClick('辅导课')"
          />
        </section>
        <section>
          <div class="section_title"><span></span> <span>邮寄地址</span></div>
          <van-field
            v-model="username"
            name="联系人"
            label="联系人"
            placeholder="请输入联系人"
          />
          <van-field
            v-model="username"
            name="手机号码"
            label="手机号码"
            placeholder="联系人手机号"
          />
          <van-field
            v-model="username"
            name="所在地区"
            label="所在地区"
            placeholder="请选择"
            :is-link="true"
            @click="fieldClick('所在地区')"
          />
          <van-field
            v-model="username"
            rows="2"
            type="textarea"
            maxlength="50"
            placeholder="输入您的详细地址"
          />
        </section>

        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit"
            >下一步</van-button
          >
        </div>
      </van-form>
    </div>
    <van-popup v-model="show" position="bottom" :style="{ height: '45%' }">
      <van-picker
        show-toolbar
        :title="picker_title"
        :columns="picker_columns"
        @confirm="picker_confirm"
        @cancel="picker_cancel"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "TestRegistrationForm",
  components: {},
  props: {},
  data() {
    return {
      username: "",
      password: "",
      show: false,
      picker_columns: [
        {
          text: "浙江",
          children: [
            {
              text: "杭州",
              children: [{ text: "西湖区" }, { text: "余杭区" }],
            },
            {
              text: "温州",
              children: [{ text: "鹿城区" }, { text: "瓯海区" }],
            },
          ],
        },
        {
          text: "福建",
          children: [
            {
              text: "福州",
              children: [{ text: "鼓楼区" }, { text: "台江区" }],
            },
            {
              text: "厦门",
              children: [{ text: "思明区" }, { text: "海沧区" }],
            },
          ],
          picker_title: "",
        },
      ],
    };
  },
  created() {},
  mounted() {},
  activated() {},
  update() {},
  methods: {
    fieldClick(e) {
      console.log(e);
      if (e == "报考点") {
        this.picker_title = "选择城市";
      }
      this.show = true;
    },
    picker_confirm() {
      console.log("确定");
      this.show = false;
    },
    picker_cancel() {
      console.log("取消");
      this.show = false;
    },
  },
  filters: {},
  computed: {},
  watch: {},
};
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>




